.search-home {
   width: 100%;
   max-width: 874px;
   margin: 0 auto;
   margin-right: 100px;
 }
 
 .inner-form-home {
   display: flex;
   flex-wrap: wrap;
 }
 
 .inner-form-search-home {
   display: flex;
   align-items: center;
   width: 70%;
   white-space: nowrap;
   border: 1px solid rgba(153, 12, 143, 0.7);
   border-radius: 70px;
   cursor: pointer;
   background: white;
   margin-left: 0;
   transition: all 0.2s ease-out, color 0.2s ease-out;
 }
 
 .inner-form-search-home input[type="text"] {
   border: none;
 }
 
 .btn-search-sh-se {
   background-color: rgba(153, 12, 143, 0.7);
   color: #fff;
   border: none;
   border-radius: 100%;
   width: 70px;
   height: 70px;
   cursor: pointer;
 }
 
 .btn-search-sh-se:hover {
   background-color: rgba(153, 12, 143, 0.7);
 }
 
 .search-results-sh {
   margin-top: 20px;
   display: flex;
   justify-content: center;
 }
 
 .clear-results {
   display: flex;
   cursor: pointer;
 }
 
 .cards-container-sh {
   display: flex;
   flex-wrap: nowrap;
   justify-content: center;
 }
 
 .card-container-sh {
   margin: 10px;
   box-sizing: border-box;
 }
 
 .inner-form-home h1{
   font-size: 3.2rem;
   text-align: center;
   margin-right: 218px;
   margin-bottom: 5px;
 
 }

/* Media queries for responsiveness */
@media screen and (max-width: 768px) {
  .inner-form-home h1 {
    margin-right: 0; /* Remove right margin */
  }
  
  .inner-form-search-home {
    width: 100%; /* Take up full width */
    margin-right: 20px; /* Add some space between input and button */
  }
  
  .btn-search-sh-se {
    width: 50px; /* Reduce button size */
    height: 50px;
  }
  
  .cards-container .MuiCard-root {
    width: 150px; /* Adjust card width */
  }
}

@media screen and (max-width: 480px) {
  .inner-form-home h1 {
    font-size: 2.2rem; /* Reduce font size */
  }
}
 