@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
}

.header {
  width: 100%;
  height: 80px;
  background: radial-gradient(circle, rgba(153, 12, 143, 0.7) 10%, #000000 100%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: background-color 0.5s ease; 
  padding: 1rem 0;
  position: relative; 
}

.navbar-brand {
  font-size: 1.5rem;
  font-weight: bold;
}

.navbar-toggler {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.navbar-toggler-icon {
  font-size: 1.25rem;
}

.header.fixed {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100; 
}

.header__buttons a {
  display: flex;
  align-items: center;
}

.header__button {
  background-color: #EA3F23;
  border: none;
  border-radius: 20px;
  margin: 3px;
  padding: 20px;
  cursor: pointer;
  color: white;
}

.header__button:hover {
  background-color: purple;
}

.navbar-nav {
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}

.navbar-nav .nav-link {
  margin-right: 15px;
  color: white;
}

.link-form {
  margin-left: 300px;
  /* margin-top: 2rem; */
  flex-direction: column;
  align-items: flex-start; 
  justify-content: center;
}


.header__user-type {
  margin-right: 10px;
  color: #fff;
  font-weight: bold;
}

@media only screen and (max-width: 1024px) {
  .navbar-collapse {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background:radial-gradient(circle, rgba(153, 12, 143, 0.7) 10%, #000000 100%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow-y: auto;
    transition: transform 0.3s ease-in-out;
    transform: translateY(-100%);
  }
  
  .navbar-collapse.show {
    transform: translateY(0);
  }

  .navbar-nav {
    flex-direction: column;
    margin-top: 2rem;
    gap: 1rem;
  }

  .navbar-nav .nav-link {
    margin: 0;
  }

  .link-form {
    margin-left: 0;
    margin-top: 2rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
