.s003-search {
  justify-content: center;
  align-items: center;
  font-family: 'Poppins', sans-serif;
  background-position: center;
  max-width: 972px;
  padding: 30px;
  margin: 0 auto; /* Center the search component horizontally */
}

.inner-form-search {
  display: flex;
  align-items: center;
  width: 100%;
  white-space: nowrap;
  border: 1px solid rgba(153, 12, 143, 0.7);
  border-radius: 70px;
  cursor: pointer;
  background: white;
  margin-left: 0;
  transition: all 0.2s ease-out, color 0.2s ease-out;
  padding: 10px; /* Adjust padding */
}

.inner-form-search input[type="text"] {
  flex: 1; /* Fill up the remaining space */
  border: none;
  outline: none;
  padding: 10px; /* Adjust padding */
}

.select-option {
  height: 60%;
  width: 120px;
  white-space: wrap;
  outline: none;
  cursor: pointer;
  background: white;
  transition: all 0.2s ease-out, color 0.2s ease-out;
  padding: 10px; /* Adjust padding */
  border: none;
}

.search-results h2 {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.search-results h2 div {
  margin-left: 20px;
}

.btn-search-sh {
  background-color: rgba(153, 12, 143, 0.7);
  color: #fff;
  border: none;
  border-radius: 100%;
  width: 50px; /* Adjust button size */
  height: 50px; /* Adjust button size */
  cursor: pointer;
}

.btn-search-sh:hover {
  background-color: rgba(153, 12, 143, 0.7);
}

.btn-icon {
  font-size: 24px; /* Default icon size */
}

.input-select ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.input-select li {
  display: inline-block;
  cursor: pointer;
}

.input-select li:hover {
  text-decoration: underline;
  color: rgba(153, 12, 143, 0.7);
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 30px; /* Add some spacing between search results and search bar */
}

/* Media query for responsiveness */
@media screen and (max-width: 600px) {
  .inner-form-search {
    flex-direction: column; /* Stack elements vertically */
    align-items: stretch; /* Stretch elements to full width */
  }

  .inner-form-search input[type="text"] {
    width: 100%; /* Take up full width */
    margin-bottom: 10px; /* Add spacing between elements */
  }

  .select-option {
    width: 100%; /* Take up full width */
  }

  .btn-search-sh {
    width: 100%; /* Take up full width */
    margin-top: 10px; /* Add spacing between elements */
  }

  .btn-icon {
    font-size: 20px; /* Adjust icon size for smaller screens */
  }
}
